
 ///////////////////////////////////////////////////////////////////////// KETA_LO_001.html
.login {
    background:url(../../images/login_background.png) no-repeat;
    background-size: cover;
    background-position: center;
}

.login_sub {
    background:url(../../images/login_background02.png) no-repeat;
    background-size: cover;
    background-position: center;
}

.login_wrap {
    width: 600px;
    margin:0 auto;
    text-align:center;
    padding:50px 0;
    &.sub {
        padding:100px 0;
    }
    h3 {
        font-size: 46px;
        font-weight: bold;
        color: #fff;
        margin-bottom: 45px;
    }
    h4 {
        font-size: 54px;
        font-weight: bold;
        color: #fff;
    }
    h5 {
        font-size:34px;
        font-weight:normal;
        color:#fff;
        margin-bottom:45px;
    }

    ul {
        li {
            margin-bottom: 45px;
            color: #e6e8e7;
            opacity: 0.8;
            img {
                margin-right: 10px;
            }

            &:first-child {
                margin-bottom: 20px;
            }
        }
    }

    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;

        input {
            width: 370px;
            padding: 20px;

            &:first-child {
                margin-bottom: 15px;
            }
        }
    }

    p {
        margin-bottom: 25px;
        color: #aaaaaa;
        font-size: 14px;

        a {
            color: #1e89db;
        }
    }

    button {
        width: 410px;
        padding: 20px;
        font-size: 18px;
        border-radius: 29px;
    }
}

///////////////////////////////////////////////////////////////////////// KETA_MI_002.html
#main {
    display:flex;
    min-height: 1000px;
    overflow:hidden;
    &::after {
        content:'';
        position: absolute;
        width:320px;
        min-height: 1000px;
        right:0;
        background:#ffffff94;
    }
    .main_left {
        min-width:870px;
        overflow:hidden;
        background:url('../../images/left_background02.png') no-repeat;
        background-size:cover;
        .main_left_contents {
            width:460px;
            height:100%;
            float:right;
            padding:9.5rem 0 0 0;
            margin-right:90px;
            a{
                margin: 40px 0;
            }
            p{
                color: #f5f5f5;
                font-size: 14px;
                margin-top:30px;
                line-height:25px;
                opacity: 0.8;
            }
            h2{
                color: #fff;
                font-size: 62px;
                line-height: 4.5rem;
            }
            h3{
                color: #de9d22;
                font-size: 45px;
                font-weight:normal;
            }
            button {
                font-size:16px;
                margin-top:30px;
            }
            .autoplay3 {
                margin-top:7rem;
                .slick-list {
                    margin-top:50px;
                }
                .main_note {
                    display:flex;
                    padding-top: 20px;
                    border-top: 1px solid #ffffff61;
                    margin-top: 30px;
                    .main_note_left {
                        h3 {
                            font-size:34px;
                            font-weight:bold;
                            color:#2f66aa;
                            line-height:25px;
                        }
                        span {
                            font-size:12px;
                            color:#fff;
                        }
                    }
                    .main_note_right {
                        p {
                            font-size:16px;
                            font-weight:300;
                            color:#fff;
                            margin:0 0 0 15px;
                        }
                    }
                }
                .slick-prev {
                    top:-20px;
                    left: 90%;
                    background:url('../../images/slick_arrow_left.png') no-repeat;
                    &::before {
                        font-size:0;
                    }
                }
                .slick-next {
                    top: -20px;
                    right: 0;
                    background:url('../../images/slick_arrow_right.png') no-repeat;
                    &::before {
                        font-size:0;
                    }
                }
            }
        }
    }
    .main_right {
        background:#f5f5f5;
        padding:30px 0 50px 50px;
        overflow: hidden;
        min-width: 1000px;
        position: relative;
  
        h1 {
            font-size:24px;
            color:#333333;
            margin-bottom:25px;
        }
        .autoplay2 {
            margin-bottom:30px;
                .slick-prev {
                    top:-30px;
                    left: 62%;
                    background:url('../../images/slick_arrow_left.png') no-repeat;
                    &::before {
                        font-size:0;
                    }
                }
                .slick-next {
                    top: -30px;
                    right: 33%;
                    background:url('../../images/slick_arrow_right.png') no-repeat;
                    &::before {
                        font-size:0;
                    }
                }
            .main_my_class{
                .class_img{
                    display: flex;
                    position: relative;
                    overflow: hidden;
                    .class_img_1 {
                        width:315px;
                        a {
                            img {
                                width:100%;
                            }
                        }
                    }
                    .class_time{
                        position: absolute;
                        top: 0;
                        margin-top: 9px;
                        color: #fff;
                        font-size: 11px;
                        border: none;
                        background: #2f66aa;
                        p{
                            padding: 4px 4px 4px 11px;
                            opacity: 0.8;
                            img{
                                margin-bottom: 2px;
                            }
                        }
                    }
                    .class_description{
                        position: absolute;
                        margin-left: 17px;
                        bottom: 25px;
                        p:first-child{
                            color: #fff;
                        }
                        p:last-child{
                            color: #fff;
                            font-size: 13px;
                            opacity: 0.5 ;
                        }
                    }
                }
            }
                .program_img{
                    display: flex;
                    .main_program_img {
                        background:#fff;
                        margin-right: 20px;
                        .program_img_top {
                            img {
                                width:100%;
                            }
                        }
                        .program_description {
                            position: relative;
                            padding:30px;
                            &.china {
                                &::before {
                                    content:'';
                                    position:absolute;
                                    top:-25px;
                                    left:20px;
                                    background:url(../../images/main_country01.png) no-repeat;
                                    width:55px;
                                    height:55px;
                                }
                            }
                             &.korea {
                                &::before {
                                    content:'';
                                    position:absolute;
                                    top:-25px;
                                    left:20px;
                                    background:url(../../images/main_country02.png) no-repeat;
                                    width:55px;
                                    height:55px;
                                }
                            }
                            &.america {
                                &::before {
                                    content:'';
                                    position:absolute;
                                    top:-25px;
                                    left:20px;
                                    background:url(../../images/main_country03.png) no-repeat;
                                    width:55px;
                                    height:55px;
                                }
                            }
                            h3{
                                color: #333333;
                                font-size: 17px;
                                margin-bottom: 10px;
                            }
                            strong{
                                color: #2f66aa;
                                font-size: 13px;
                                font-weight: 700;
                            }
                            span {
                                font-size:14px;
                                color:#777777;
                            }
                        }
                    }
                    .main_program_img:last-child{
                        .program_img_top{
                            img{
                                height: 186.22px;
                            }
                        }
                    }
                }
            .main_info_top{
                h1{
                    display: inline-block;
                    margin-right: 140px;
                }
                img{
                    margin-left: 11px;
                    vertical-align: initial;
                }
            }
            .information_description{ 
                .info_card{
                    display: inline-block;
                    width:245px;
                    padding: 35px;
                    background: #bf5b4a;
                    border-radius: 8px;
                    .info_card_right {
                        float:right;
                        margin-top: 10px;
                    }
                    span{
                        color: #fff;
                        margin-left:10px;
                    }
                    &.color2 {
                        background: #dca741;
                    }
                    &.color3 {
                        background:#4d907e;
                    }
                }
            }
        }
        div {
            h1{
                margin-bottom: 15px;
                font-size: 24px;
                margin-top: 40px;
            }
            &:first-child{
                h1 {
                    margin-top:0;
                }  
            }
        }
    }
}
